body{background: #ffffff;font-family: 'Segoe UI', Tahoma, Arial, sans-serif;-webkit-font-smoothing: antialiased; margin: 0;}


.content-wapper{margin-top: 100px !important;padding-left: 10px;padding-right: 10px;}
.header{position: fixed;left: 0;right: 0;top: 53px;z-index: 9;background-color: #ffffff;}
.header.header-fixed-top{background-color: #ffffff; box-shadow: 0 2px 5px rgba(0,0,0,.08); top: 0;}
.header-top-add { background: linear-gradient( to right, #0E416F, #0E416F,  #C91717); color: #fff; font-size: 20px; padding: 10px 0;}
.header-top-add img { vertical-align: text-top;}
.ms-headtext{color: #fff;text-decoration: none;cursor: unset;}    
.nav-right-section{left: 50%;position: absolute;display: inline-flex;width: 100%;align-items: center;}
.country-ddl{top: 7px;}
.user-profile{float:right;}
.user-profile img{border-radius: 50% !important;    width: 34px;align-self: center; margin-left: 9px;}
.user-profile .root-125{
    padding-left: 0 !important;
    padding-right: 0 !important;
  }


.container { padding: 0 8px; }
.textWhite { color: #FFFFFF !important; }
.footerLinks ul { padding: 0; list-style: none; }
.footerLinks ul li { margin-bottom: 5px; }
.footerLinks ul li a { color: #FFFFFF; font-size: 14px; }
.footerLinks ul li a:hover { color: #FFFFFF; text-decoration: none; }
@media ( min-width: 1240px ) {
  .container { width: 1200px; margin: auto;}
}